@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  overflow-x: hidden;
  box-sizing: border-box;
}

.search-icon.active {
  left: 2.3rem !important;
}

.input.active {
  margin-left: 0.75rem !important;
}

@media screen and (max-width: 767px) {
  .input.active {
    position: absolute;
    top: -0.85rem;
    left: 0.5rem;
    margin-left: 0.75rem !important;
    width: 15.5rem !important;
  }
}
